
.header{
  background-color: #f0b411;
}

img{
  width:2rem;
  height:2rem;
}
h1 {
  text-align: center;
  background-color: #f4a226;
  font-family: "Monoton";
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 10px;
  letter-spacing: 0px;
  padding: 2.4rem 0;
  margin-top: 0px;
}

body{
  margin:0px;
  font-family: sans-serif;
  color: #5a3e2b;
  font-family: "Quicksand";
  font-weight: 500;
}


.segment{
  width: 25px;
  height: 6.25px;
  background-color: #3498db; /* Change the background color as needed. */
  position: absolute;
  clip-path: polygon(16.6% 0%, 83.3% 0%, 100% 50%, 83% 100%, 16% 100%, 0% 50%);
  background-color: transparent;

}

.segmentRed{
  background-color:red;
}

.sevenSegmentPosition{
  left:1300px;
  position:absolute; 

}

.overall{
  margin: 0 auto;
  background-color: bisque;
  width:auto;
  height:100vh;
  align-items: center;
  justify-content: center;
 text-align: center;
}

.grid-container {
  
  display: grid;
  text-align: center;
  grid-template-columns: 37em 37rem ; 
  grid-gap: 10px; /* Add 10px spacing between grid items */
}

.React-title{
  font-size: 10rem;;
}

